
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'IcoIsTrue',
  props: {
    isTrue: Boolean
  },
  computed: {
    url () {
      if (this.isTrue) {
        return require('@/assets/ico/greenCheckMark.png')
      }

      return require('@/assets/ico/Redcross.png')
    }
  }
})
